<template>
    <header>
        <h1>{{title}}</h1>
        <aButton @btn-click="$emit('toggle-add-task')" :text="showAddTask ? 'Close' : 'addTask'" :color="showAddTask ? 'red' : 'green'" />
    </header>
</template>

<script>
import aButton from './aButton.vue'

export default {
    name: "aHeader",
    props: {
        title: String,
        showAddTask: Boolean,
    },
    components: { 
        aButton
    },
    emits: ['toggle-add-task']
}
</script>

<style scoped>
header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 20px;
}
</style>