<template>
    <div :key="task.id" v-for="task in tasks">
    <aTask @toggle-reminder="$emit('toggle-reminder', task.id)" @delete-task="$emit('delete-task', task.id)" :task="task"/>
    </div>
</template>

<script>
import aTask from './aTask'

export default {
    name: 'aTasks',
    props: {
        tasks: Array,
    },
    components: {
        aTask
    },
    emits: ['delete-task', 'toggle-reminder'],
}
</script>