<template>
  <div class="container">
    <aHeader @toggle-add-task="toggleAddTask" title="Hello World" :showAddTask="showAddTask" />
    <div v-if="showAddTask">
        <AddTask @add-task=addTask />
    </div>
    <aTasks @toggle-reminder="toggleReminder" @delete-task="deleteTask" :tasks="tasks" />
  </div>
</template>

<script>
import aHeader from './components/aHeader'
import aTasks from './components/aTasks'
import addTask from './components/addTask'
import AddTask from './components/addTask.vue'

export default {
  name: 'App',
  components: {
    aHeader,
    aTasks,
    addTask,
    AddTask
},
  methods: {
    addTask(task) {
      this.tasks = [...this.tasks, task]
    },
    deleteTask(id) {
      if (confirm('Are you sure you want to delete ' + this.tasks.find((task) => task.id === id).text))
      this.tasks = this.tasks.filter((task) => task.id !== id)
    },
    toggleReminder(id) {
      this.tasks = this.tasks.map((task) => task.id === id ? {...task, reminder: !task.reminder}: task)
    },
    toggleAddTask() {
      this.showAddTask = !this.showAddTask
    }
    
  },
  data() {
    return {
      tasks: [],
      showAddTask: false
    }
  },
  created() {
    this.tasks = [
      {
        id: 1,
        text: 'doctor',
        day: 'March 1st at 2:30pm',
        reminder: true,
      },
    ]
  }
}
</script>

